<script>
import axios from 'axios'

export default {
  props: {
    url: {
      type: String,
      default: 'https://apia.aidioute.cn/weather/'
    }
  },
  data: () => ({
    updating: false,
    latitude: '',
    longitude: '',
    weatherData: null,
    weathercode: 'd00',
    timer: null
  }),
  methods: {
    isObject (obj) {
      return obj !== null && Object.prototype.toString.call(obj) === '[object Object]'
    },
    handleSendError (data) {
      this.$emit('notice', data)
    },
    handleGetLocation () {
      if (typeof window !== 'undefined' && window.navigator.geolocation) {
        window.navigator.geolocation.getCurrentPosition(
          (result) => {
            this.latitude = result.coords.latitude.toFixed(6)
            this.longitude = result.coords.longitude.toFixed(6)
          },
          (error) => {
            switch (error.code) {
              case 0:
                this.handleSendError({
                  type: 'warning',
                  from: 'window.navigator.geolocation',
                  msg: ''
                })
                break
              case 1:
                this.handleSendError({
                  type: 'warning',
                  from: 'window.navigator.geolocation',
                  msg: ''
                })
                break
              case 2:
                this.handleSendError({
                  type: 'warning',
                  from: 'window.navigator.geolocation',
                  msg: ''
                })
                break
              case 3:
                this.handleSendError({
                  type: 'warning',
                  from: 'window.navigator.geolocation',
                  msg: ''
                })
                break
            }
          }
        )
      } else {
        this.handleSendError({
          type: 'warning',
          from: 'window.navigator.geolocation',
          msg: ' HTML5 '
        })
      }
    },
    handleUpdate () {
      if (!this.updating) {
        this.updating = true
        this.timer && clearInterval(this.timer)
        this.handleGetWeather()
        this.timer = setInterval(() => {
          this.handleGetWeather()
        }, 30 * 60 * 1000)
      }
    },
    async handleGetWeather () {
      const url = this.latitude && this.longitude
        ? `${this.url}?location_type=1&lat=${this.latitude}&lng=${this.longitude}&from=vmweather`
        : `${this.url}?location_type=0&from=vmweather`
      try {
        const response = await axios.get(url)
        if (response.status === 200) {
          const { data: result } = response
          if (this.isObject(result) &&
            'code' in result &&
            'data' in result &&
            result.code === 0
          ) {
            if (this.isObject(result.data) &&
              'location' in result.data &&
              this.isObject(result.data.location) &&
              'error_msg' in result.data.location
            ) {
              console.log(`; status: 200; error: ${result.data.location.error_msg}`)
              this.handleSendError({
                type: 'warning',
                from: 'server',
                msg: result.data.location.error_msg
              })
            }
            this.weatherData = result.data.weather
            this.weathercode = result.data.weather.weathercode
            this.updating = false
          } else {
            this.updating = false
            if (this.isObject(result) && 'msg' in result) {
              console.log(`; status: 200; error: ${result.msg}`)
              this.handleSendError({
                type: 'error',
                from: 'server',
                msg: result.msg
              })
            } else {
              console.log('; status: 200; error: ')
              this.handleSendError({
                type: 'error',
                from: 'server',
                msg: ''
              })
            }
          }
        }
      } catch (err) {
        this.updating = false
        console.log(`; status: ${err.response.status};`)
        this.handleSendError({
          type: 'error',
          from: 'axios.error',
          msg: ''
        })
      }
    }
  },
  created () {
    this.handleGetLocation()
    this.handleUpdate()
  },
  beforeDestroy () {
    this.timer && clearInterval(this.timer)
  }
}
</script>

<template>
  <div @click="handleUpdate" :style="`cursor: ${updating? 'not-allowed': 'pointer'}; user-select: none;`">
    <span v-if="updating">...</span>
    <slot v-if="!updating" :weather="weatherData" :icon="weathercode"></slot>
  </div>
</template>
